@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    margin: 0;
    padding: 0;
    font-family: "Jost", sans-serif;
}

body {
    /* background: #f3f2ef; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "Jost", sans-serif;
}

body::before {
    content: "";
    position: absolute;
    background: url(/WeatherApp/static/media/WSjKlp7.2f8bf1ef.jpg) no-repeat center center/cover !important;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.9;
}

html {
    font-size: 100%;
    height: 100%;
}

html {
    background: #eee;
}

.box {
    width: 30vw;
    transition: all;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background: inherit;
    /* background: #a5bbdd; */
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    min-width: 25rem;
    min-height: 35rem;
}

.box:hover {
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.inputData {
    width: 100%;
    margin-top: 0.8rem;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inputField {
    width: 50%;
    margin: auto;
    border-radius: 3rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.5rem;
}

.wave {
    opacity: 0.3;
    /* ab position absolute ho gyi toh agr iska parent relative h toh acha h agr nhi toh ye body ko parent manega or agr parent relative h toh uske according chelga  */
    position: absolute;
    /* then box se top 120% and left 50%  */
    top: 120%;
    left: 50%;
    background: #fff;
    width: 50rem;
    height: 50rem;
    /* ab wo bhaut niche chla gya toh usa wapas upar lane ke liye ye code */
    margin-left: -25rem;
    margin-top: -26rem;
    -webkit-transform-origin: 50% 48%;
            transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 3000ms infinite linear;
            animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave.-three {
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}

.wave.-two {
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    opacity: 0.1;
    z-index: 3 !important;
}


