@-webkit-keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes drift {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.info {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
}

.location {
    margin-top: 8rem;
    /* margin-top: 1.5rem; */
    text-align: center;
    font-weight: 800;
    font-size: 5rem;
    text-transform: capitalize;
}

.fa-street-view {
    -webkit-animation: rotates 3s ease-in-out infinite alternate;
            animation: rotates 3s ease-in-out infinite alternate;
}

@-webkit-keyframes rotates {
    from {
        -webkit-transform: translateX(-1.8rem);
                transform: translateX(-1.8rem);
    }
    to {
        -webkit-transform: translateX(0.1rem);
                transform: translateX(0.1rem);
    }
}

@keyframes rotates {
    from {
        -webkit-transform: translateX(-1.8rem);
                transform: translateX(-1.8rem);
    }
    to {
        -webkit-transform: translateX(0.1rem);
                transform: translateX(0.1rem);
    }
}

#date {
    text-align: center;
    margin-top: 0.5rem;
    color: #57606f;
    font-size: 1.2rem;
    font-weight: 300;
    text-transform: uppercase;
}

.temp {
    margin-top: 2.5rem;
    text-align: center;
    font-size: 3rem;
}

.tempmin_max {
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}


