@media (max-width: 600px) {
    .box {
        width: 90vw;
        height: 80vh;
    }
    .wave {
        top: 115%;
    }
    #weathercon {
        font-size: 5em;
    }
    .info {
        font-size: 1.5em;
    }
}

body>span {
    width: 100vw;
    text-align: center;
    color: grey;
}

.errorMsg {
    text-align: center;
    font-size: 2rem;
    margin: 2rem auto;
}
